.swal-text,
.swal-modal {
    height: unset;
}

.swal-overlay {
    background-color: rgba(59, 44, 53, 0.65)
}

.swal-modal {
    background-color: $black;

}

.swal-text {
    color: $white;
}

.swal-button {
    background-color: $secondaryAccent;
    &:hover {
        background-color: $secondaryColor;
    }
}